order-box;\n  padding: 0px 0px 70px 0px;\n  h3{\n    height: 90px;\n    line-height: 90px;\n    background-color: $textBlueColor;\n    color: $whiteColor;\n    border-radius: 15px 15px 0px 0px;\n    text-align: center;\n  }\n  .field{\n    width: 95%;\n    margin: auto;\n    .van-field{\n      margin: 35px 0px;\n    }\n    .van-cell::after{\n      border: none;\n    }\n    .van-field__clear{\n      font-size: 30px;\n    }\n    .van-field__control{\n      height: 8vw;\n      line-height: 8vw;\n    }\n    .van-cell{\n      font-size: 30px;\n      padding: 0px;\n      .van-field__label{\n        height: 70px;\n        line-height: 70px;\n      }\n      .van-cell__value{\n        height: 70px;\n        display: flex;\n        align-items: center;\n        border-bottom: 1px solid $greyColor;\n        box-sizing: border-box;\n        padding: 10px;\n      }\n      .van-field__body{\n        width: 100%;\n      }\n    }\n  }\n  .but{\n    width: 80%;\n    margin: 70px auto auto auto;\n    display: flex;\n    flex-wrap: nowrap;\n    justify-content: space-around;\n    button{\n      width: 40%;\n      height: 75px;\n      background-color: $textBlueColor;\n      border: none;\n      border-radius: 100px;\n      color: $whiteColor;\n    }\n  }\n}\n.guanbi-but{\n  margin-top: 150px;\n  text-align: center;\n  i{\n    font-size: 70px;\n    color: $whiteColor;\n  }\n}\n/* 消费者 商品兑换*/\n.consumer {\n  height: 100%;\n  position: relative;\n  overflow: hidden;\n  .scan-code {\n    // height: 100%;\n\n    i {\n      display: block;\n      text-align: center;\n      font-size: 100px;\n      color: $textBlueColor;\n      margin-top: 100px;\n    }\n\n    p {\n      padding: 0px 100px;\n      text-align: left;\n      text-align: justify;\n      box-sizing: border-box;\n      color: $greyColor;\n      margin-top: 15px;\n    }\n\n    button {\n      display: block;\n      width: 50%;\n      height: 70px;\n      margin: 100px auto auto auto;\n      border: none;\n      background-color: $blueColor;\n      color: $whiteColor;\n      font-size: 30px;\n      border-radius: 100px;\n    }\n  }\n\n  .commodity-exchange {\n    height: 100%;\n    overflow: auto;\n    background-color: rgb(235, 235, 235);\n    ul{\n      margin:30px auto 258px auto;\n      li{\n        box-sizing: border-box;\n        width: 95%;\n        margin: auto auto 30px auto;\n        background-color: $whiteColor;\n        border-radius: 20px;\n        overflow: hidden;\n        box-shadow: 10px 10px 5px $bgGreyColor;\n        .van-button{\n          width: 100px;\n          height: 100%;\n          border-radius: 0px 20px 20px 0px;\n          background-color: $mainColor;\n          border:1px solid $mainColor;\n          font-size: 30px;\n        }\n        .van-stepper__plus{\n          background-color: $blueColor;\n        }\n        .van-stepper__input{\n          width: 50px;\n          font-size: 30px;\n          // color: $whiteColor;\n        }\n        .van-stepper__minus{\n          color: $whiteColor;\n          border: 1px solid $orangeColor;\n          background-color: $mainColor;\n        }\n        .content{\n          width: 100%;\n          box-sizing: border-box;\n          padding: 15px 0px;\n          p{\n            line-height: 80px;\n            // color: $whiteColor;\n            box-sizing: border-box;\n            overflow: hidden;\n            white-space: nowrap;\n            text-align:left;\n            text-align: justify;\n          }\n          .title{\n            width: 70%;\n            margin: auto;\n            text-overflow: ellipsis;\n            text-align: center;\n          }\n          .l-r{\n            width: 95%;\n            margin: auto;\n            display: flex;\n            flex-wrap: nowrap;\n            justify-content: space-between;\n            align-items: center;\n            .l{\n              width: 70%;\n              display: flex;\n              align-items: center;\n              flex-wrap: nowrap;\n              overflow: hidden;\n              i{\n                margin-right: 15px;\n                color: $orangeColor;\n                font-size: 50px;\n              }\n              b{\n                color: $mainColor;\n              }\n            }\n            .r{\n              width: 30%;\n              box-sizing: border-box;\n              padding-left: 15px;\n              text-align: right;\n            }\n          }\n        }\n      }\n    }\n\n    .footer {\n      width: 100%;\n      position: absolute;\n      bottom: 0px;\n      button{\n        width: 100%;\n        height: 100px;\n        background-color: $blueColor;\n        border: none;\n        color: $whiteColor;\n        font-size: 38px;\n      }\n      .footer-text{\n        width: 100%;\n        height: 130px;\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        .left,.right{\n          display: flex;\n          flex-direction: column;\n          align-items: center;\n          justify-content: center;\n          height: 100%;\n        }\n        .left{\n          width: 50%;\n          background-color: $whiteColor;\n          .total,.my-dragoncoin{\n            width: 95%;\n            margin: 0 auto;\n            white-space: nowrap;\n            overflow: hidden;\n          }\n          .total{\n            color:$mainColor;\n          }\n          .my-dragoncoin{\n            color: $greyColor;\n          }\n        }\n        .settlement{\n          width: 30%;\n          height: 100%;\n          line-height: 130px;\n          text-align: center;\n          background-color: $orangeColor;\n          color: $whiteColor;\n          font-size: 38px;\n        }\n        .router{\n          width: 20%;\n          height: 100%;\n        }\n        .right{\n          width: 100%;\n          background-color: $bgGreyColor;\n          color:$whiteColor;\n          .my-icon{\n            font-size: 30px;\n          }\n        }\n      }\n    }\n  }\n}\n/* 兑换成功 */\n.success{\n  height: 100%;\n  overflow: hidden;\n  .icon-success{\n    display: block;\n    text-align: center;\n    font-size: 150px;\n    color: $greenColor;\n    margin-top: 50px;\n  }\n  .success-text{\n    text-align: center;\n    color: $greenColor;\n    margin: 25px 0px 80px 0px;\n    font-size: 35px;\n  }\n  .details-time{\n    width: 100%;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n    overflow: hidden;\n    box-sizing: border-box;\n    padding: 0px 25px;\n    margin: 25px 0px;\n    .span1{\n      width: 25%;\n    }\n    .span2{\n      width: 75%;\n      text-align: right;\n    }\n    .span1,.span2{\n      white-space: nowrap;\n      overflow: hidden;\n    }\n    .gift-bag{\n      width: 55%;\n      overflow: hidden;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n    }\n    .number{\n      width: 10%;\n      overflow: hidden;\n      white-space: nowrap;\n      text-align: center;\n    }\n    .long-bi{\n      width: 35%;\n      overflow: hidden;\n      white-space: nowrap;\n      text-align: right;\n      padding: 0px 15px;\n      box-sizing: border-box;\n    }\n  }\n}\n/* 我的 */\n.personalcenter{\n  height: 100%;\n  .per-head{\n    height: 340px;\n    background-image: linear-gradient(to top, $skyBlueColor, $blueColor);\n    overflow: hidden;\n    img{\n      width: 200px;\n      height: 200px;\n      display: block;\n      margin: 35px auto 25px auto;\n      border-radius: 100px;\n    }\n    p{\n      width: 50%;\n      text-align: center;\n      overflow: hidden;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n      margin: auto;\n      color: $whiteColor;\n    }\n  }\n  ul{\n    width: 95%;\n    margin: auto;\n    li{\n      display: flex;\n      flex-wrap: nowrap;\n      justify-content: space-between;\n      align-items: center;\n      height: 100px;\n      border-bottom: 1px solid $LightGreyColor;\n      color: $blackColor;\n      .icon1{\n        font-size: 40px;\n        color: $textBlueColor;\n      }\n      span{\n        width: 80%;\n      }\n      .icon2{\n        font-size: 35px;\n      }\n    }\n  }\n}\n/* 我的订单 */\n.myorder{\n  height: 100%;\n  background-color: $LightGreyColor;\n  ul{\n    overflow: hidden;\n    li{\n      width: 100%;\n      margin:auto auto 20px auto;\n      box-sizing: border-box;\n      padding: 0px 30px;\n      background-color: $whiteColor;\n      .border{\n        border-top: 1px solid $LightGreyColor;\n      }\n      p{\n        width: 100%;\n        overflow: hidden;\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: space-between;\n        box-sizing: border-box;\n        line-height:110px;\n        span{\n          white-space: nowrap;\n          overflow: hidden;\n        }\n        .name{\n          width: 45%;\n        }\n        .number{\n          width: 15%;\n          text-align: center;\n          box-sizing: border-box;\n          padding: 0px 10px;\n        }\n        .longbi{\n          width: 40%;\n          text-align: right;\n          color: $mainColor;\n        }\n        .longbi-icon{\n          font-size: 35px;\n          color: $orangeColor;\n          margin-right: 10px;\n        }\n        .left{\n          width: 50%;\n        }\n        .right{\n          width: 50%;\n          text-align: right;\n        }\n        .color1{\n          color: $textBlueColor;\n        }\n        .color2{\n          color: $mainColor;\n        }\n      }\n    }\n  }\n}\n/* 商家 */\n.business{\n  height: 100%;\n  background-color: $LightGreyColor;\n  .router{\n    color: $whiteColor;\n  }\n\n.commodity-order{\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  justify-content: space-between;\n  background-color: $whiteColor;\n  box-sizing: border-box;\n  padding-bottom: 45px;\n  .router-link{\n    width: 50%;\n    color: $blackColor;\n    .left-right{\n      width: 100%;\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      margin-top: 50px;\n      .commodity,.order{\n        width: 180px;\n        height: 180px;\n        color: $whiteColor;\n        text-align: center;\n        line-height: 180px;\n        border-radius: 15px;\n        margin-bottom: 20px;\n        i{\n          font-size: 80px;\n        }\n      }\n      .commodity{\n        background-color:$skyBlueColor;\n      }\n      .order{\n        background-color:$pinkColor;\n      }\n    }\n  }\n}\n}\n/* 商品管理 */\n.commodityadmin::-webkit-scrollbar {\n  display: none;\n}\n.commodityadmin{\n  height: 100%;\n  background-color: $LightGreyColor;\n  overflow-x: hidden;\n  overflow-y: scroll;\n  ul{\n    width: 95%;\n    margin: auto;\n    box-sizing: border-box;\n    padding-bottom: 25px;\n    li{\n      box-sizing: border-box;\n      padding: 25px;\n      background-color: $whiteColor;\n      margin-top: 25px;\n      border-radius: 15px;\n      box-shadow: 10px 10px 5px $bgGreyColor;\n      .score{\n        color: $mainColor;\n      }\n      p{\n        line-height: 65px;\n        overflow: hidden;\n        white-space: nowrap;\n      }\n      .flex{\n        display: flex;\n        flex-wrap: nowrap;\n        justify-content: space-between;\n        align-items: center;\n        span{\n          box-sizing: border-box;\n          white-space: nowrap;\n          overflow: hidden;\n        }\n        .sold{\n          width: 50%;\n          color: $greyColor;\n        }\n      }\n    }\n  }\n}\n/* 订单管理 */\n.orderadmin{\n  height: 100%;\n  background-color: $LightGreyColor;\n  .van-tab{\n    font-size: 35px !important;\n    line-height: 100px !important;\n    color: $bgGreyColor !important;\n    font-weight: 700;\n  }\n  .van-tab--active{\n    color: $whiteColor !important;\n  }\n  .van-tabs__wrap{\n    height: 100px !important;\n  }\n  .van-tabs__nav{\n    background-color: $textBlueColor;\n  }\n  .van-tabs__line{\n    width: 100px;\n    height: 6px;\n    background-color: $skyBlueColor;\n  }\n.list{\n  .date-query{\n    background-color: $whiteColor;\n    .date{\n    height: 150px;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: center;\n    .time{\n      width: 30%;\n      height: 70px;\n      line-height: 70px;\n      text-align: center;\n      border: 1px solid $greyColor;\n      white-space: nowrap;\n      overflow: hidden;\n    }\n    .zhi{\n      margin: 0px 20px;\n    }\n  }\n  .query-but{\n    width:60%;\n    height: 90px;\n    display: block;\n    margin: auto;\n    border: none;\n    background-color: $skyBlueColor;\n    color: $whiteColor;\n    border-radius: 100px;\n    font-weight: 700;\n  }\n}\n  .total{\n    height: 100px;\n    display: flex;\n    flex-wrap: nowrap;\n    align-items: center;\n    justify-content: space-between;\n    background-color: $whiteColor;\n    box-sizing: border-box;\n    padding: 0px 30px;\n    border-bottom: 1px solid $LightGreyColor;\n    .router{\n      color: $textBlueColor;\n    }\n    span{\n      width: 50%;\n      white-space: nowrap;\n      overflow: hidden;\n    }\n    .mingxi{\n      text-align: right;\n    }\n  }\n  ul{\n    overflow: hidden;\n    li{\n      width: 100%;\n      margin:auto auto 20px auto;\n      box-sizing: border-box;\n      padding: 0px 30px;\n      background-color: $whiteColor;\n      .border{\n        border-top: 1px solid $LightGreyColor;\n      }\n      p{\n        width: 100%;\n        overflow: hidden;\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: space-between;\n        box-sizing: border-box;\n        line-height:110px;\n        span{\n          white-space: nowrap;\n          overflow: hidden;\n        }\n        .name{\n          width: 45%;\n        }\n        .number{\n          width: 15%;\n          text-align: center;\n          box-sizing: border-box;\n          padding: 0px 10px;\n        }\n        .longbi{\n          width: 40%;\n          text-align: right;\n          color: $mainColor;\n        }\n        .longbi-icon{\n          font-size: 35px;\n          color: $orangeColor;\n          margin-right: 10px;\n        }\n        .left{\n          width: 50%;\n        }\n        .right{\n          width: 50%;\n          text-align: right;\n        }\n        .color1{\n          color: $textBlueColor;\n        }\n        .color2{\n          color: $mainColor;\n        }\n      }\n    }\n  }\n}\n}\n/* 全局日历控件 */\n.van-calendar__header-title{\n  font-size: 35px !important;\n  height: 70px !important;\n  line-height: 70px !important;\n}\n.van-popup__close-icon{\n  font-size: 38px !important;\n}\n.van-calendar__header-subtitle{\n  font-size: 30px;\n}\n.van-calendar__weekday{\n  font-size: 30px;\n  height: 70px;\n  line-height: 70px;\n}\n.van-calendar__day{\n  font-size: 30px;\n}\n.van-calendar__confirm{\n  height: 70px;\n  line-height: 70px;\n  font-size: 35px;\n}\n.van-calendar__month-title{\n  font-size: 30px;\n}\n/* 头部样式全局 */\n.head{\n  width: 100%;\n  height: 250px;\n  color: $whiteColor;\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  background-image: linear-gradient(to left, #19D5FD, #1D62F0);\n  img{\n    width: 160px;\n    height: 160px;\n    border-radius: 100px;\n    margin: 0px 30px;\n  }\n  .head-text{\n    width: 67%;\n    height: 160px;\n    line-height: 53px;\n    overflow: hidden;\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    justify-content: center;\n    p{\n      white-space: nowrap;\n      width: 100%;\n      text-align: left;\n      .renzheng{\n        background-color: $orangeColor;\n        border-radius: 10px;\n        box-sizing: border-box;\n        padding: 2px 10px;\n      }\n    }\n    .nickname{\n      width: 100%;\n      white-space: nowrap;\n      overflow: hidden;\n      text-overflow: ellipsis;\n    }\n  }\n}\n/* 提现 */\n.withdrawal{\n  height: 100%;\n  ul{\n    li{\n      width: 90%;\n      height: 150px;\n      margin: auto;\n      display: flex;\n      align-items: center;\n      border-bottom: 1px solid $LightGreyColor;\n      .van-field__clear{\n        font-size: 30px;\n      }\n      .van-cell{\n        font-size: 30px;\n        padding: 0px;\n        .van-field__label{\n          height: 70px;\n          line-height: 70px;\n        }\n        .van-cell__value{\n          height: 70px;\n          display: flex;\n          align-items: center;\n          border: 1px solid $greyColor;\n          box-sizing: border-box;\n          padding: 10px;\n        }\n        .van-field__body{\n          width: 100%;\n        }\n        \n      }\n    }\n  }\n}\n/* 配送员微信入口 */\n.deliveryman{\n  height: 100%;\n  ul{\n      display: flex;\n      flex-wrap: nowrap;\n      align-items: center;\n      justify-content: space-between;\n    .router-link{\n      width: 33%;\n      color: $blackColor;\n    }\n    li{\n      display: flex;\n      align-items: center;\n      flex-direction: column;\n      margin-top: 50px;\n      .view-shop,.add-inventory,.delivery-record{\n        width: 170px;\n        height: 170px;\n        line-height: 170px;\n        text-align: center;\n        color: $whiteColor;\n        border-radius: 15px;\n        margin-bottom: 20px;\n        i{\n          font-size: 90px;\n        }\n      }\n      .view-shop{\n        background-color: $cyanColor;\n      }\n      .add-inventory{\n        background-color: $pinkColor;\n      }\n      .delivery-record{\n        background-color: $haiColor;\n      }\n    }\n  }\n}\n/* 查看店铺 */\n.viewshop{\n  height: 100%;\n  ul{\n    li{\n      width: 95%;\n      margin: auto;\n      box-sizing: border-box;\n      padding: 15px 0px;\n      // border-bottom: 1px solid $greyColor;\n      .shop-info{\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: space-between;\n        box-sizing: border-box;\n      padding: 20px 0px;\n        .chakandianpu{\n          width: 20%;\n          font-size: 100px;\n          text-align: center;\n          color: $textBlueColor;\n        }\n        .cont-text{\n          width: 57%;\n          display: flex;\n          flex-direction: column;\n          margin-right: 15px;\n          .name-title{\n            width: 100%;\n            word-break: break-all;\n            text-overflow: ellipsis;\n            display: -webkit-box;\n            -webkit-box-orient: vertical;\n            -webkit-line-clamp: 2;\n            overflow: hidden;\n          }\n          .phone{\n            width: 100%;\n            white-space: nowrap;\n            overflow: hidden;\n            i{\n              font-size: 30px;\n              margin-left: 15px;\n              color: $textBlueColor;\n            }\n          }\n        }\n        .query-commodity{\n          width: 22%;\n          height: 60px;\n          line-height: 60px;\n          border: 1px solid $textBlueColor;\n          text-align: center;\n          border-radius: 15px;\n          color: $textBlueColor;\n        }\n      }\n      .address{\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: space-between;\n        border-top: 1px solid $LightGreyColor;\n        box-sizing: border-box;\n        padding: 20px 0px;\n        span{\n          width: 75%;\n          text-align:left;\n          text-align: justify;\n          color: $greyColor;\n        }\n        i{\n          width: 20%;\n          text-align: center;\n          font-size: 50px;\n          border-left: 1px solid $greyColor;\n          color: $textBlueColor;\n        }\n      }\n    }\n  }\n}\n/* 添加库存 */\n.addinventory{\n  height: 100%;\n  position: relative;\n  overflow: hidden;\n  .saoma-add{\n    width: 80%;\n    height: 100px;\n    background-color: $textBlueColor;\n    color: $whiteColor;\n    border: none;\n    border-radius: 100px;\n    display: block;\n    margin: 50% auto auto auto;\n    font-size: 35px;\n    font-weight: 700;\n  }\n  .add{\n    heigh